









import { Component, Prop, Vue } from 'vue-property-decorator'
import { getAbout } from '../../../api/blog'

@Component({
})
export default class extends Vue {
  private async hadnleClick() {
    const { data } = await getAbout()
    this.$router.push({
      path: '/about',
      query: {
        articleId: data[0].article_id
      }
    })
  }
}
